.nav{
    position: fixed;
    top: 0;
    width: 100%;
    display: flex;
    padding: 20px;
    justify-content: space-between;
    height: 30px;
    z-index:1;
    transition-timing-function: ease-in;
    transition: all 0.5s;
    
}
.nav_black{
    background-color: #111;
}

.nav_logo{
    position: fixed;
    left: 20px;
    width: 80px;
    object-fit: contain;
}

.nav_avatar{
    position: fixed;
    right: 20px;
    width:30px;
    object-fit: contain;

}